<template>
    <div class="wp">
        <div id="testb"></div>
        <textarea type="text" id="inputId" class="ipt-cls" placeholder="TEST" />
        <span style="font-size: 20px; margin-left: 5px">{{ assetNoLength }}</span>
    </div>
</template>

<script>
export default {
    data() {
        return {
            assetNoLength: 0,
        };
    },
    mounted() {
        const inputEle = document.getElementById('inputId');
        const qrEle = document.getElementById('testb');

        const option = {
            text: 'TEST', // 要生成二维码的网站链接 可以携带参数，例如：
            width: 150, // 存放二维码盒子的宽度，px
            height: 150, // 存放二维码盒子的高度，px
            colorDark: '#000000', // 二维码颜色，黑白相间
            colorLight: '#ffffff',
            // eslint-disable-next-line no-undef
            correctLevel: QRCode.CorrectLevel.H,
        };

        // eslint-disable-next-line no-undef
        let qrcode = new QRCode(qrEle, option);

        inputEle.oninput = e => {
            let value = e.target.value;
            if (!value) {
                value = 'TEST';
            }

            this.assetNoLength = value.length;
            qrcode.clear();
            qrcode.makeCode(value); // make another code.
        };
    },
};
</script>

